import { defHttp } from '/@/utils/http/axios';
import type { SelectProps } from 'ant-design-vue';
import { ref } from 'vue';

enum Api {
  //顶端指标
  getTopIndexUrl = '/da/asset-page/index',

  //电站规模
  getStationStatisticsUrl = '/da/asset-page/station/statistics',

  //出入库统计
  getWarehouseStatisticsUrl = '/da/asset-page/warehouse/statistics',

  //设备统计
  getEquipmentStatisticsUrl = '/da/asset-page/equipment/statistics',
  //备品备件统计
  getSpareStatisticsUrl = '/da/asset-page/spare/statistics',

  getEquipmentTypeUrl = '/da/asset-page/equipment/type',

  getSpareTypeUrl = '/da/asset-page/spare/type',
}

export const getTopIndexData = (params) => defHttp.post({ url: Api.getTopIndexUrl, params }, {});

export const getStationStatisticData = (params) =>
  defHttp.post({ url: Api.getStationStatisticsUrl, params }, {});

export const getWarehouseStatisticsData = (params) =>
  defHttp.post({ url: Api.getWarehouseStatisticsUrl, params }, {});

export const getEquipmentStatisticsData = (params) =>
  defHttp.post({ url: Api.getEquipmentStatisticsUrl, params }, {});

export const getSpareStatisticsData = (params) =>
  defHttp.post({ url: Api.getSpareStatisticsUrl, params }, {});

export const getEquipmentTypeData = (params) =>
  defHttp.get({ url: Api.getEquipmentTypeUrl, params }, {});

export const getSpareTypeData = (params) => defHttp.get({ url: Api.getSpareTypeUrl, params }, {});

export const optionsSpareType = ref<SelectProps['options']>([
  { value: '1', label: '入库单' },
  { value: '2', label: '出库单' },
  { value: '3', label: '盘点单' },
]);
